31);\r\n  --width-window-mainscreen: 360px;\r\n}\r\n\r\nbody {\r\n  font-family: var(--font-poppins);\r\n  background-color: rgb(27, 52, 82);\r\n  display: flex;\r\n  justify-content: center;\r\n}\r\n\r\n* {\r\n  margin: 0;\r\n  padding: 0;\r\n  border: none;\r\n}\r\n\r\n/* animations */\r\n\r\n@keyframes fadeIn {\r\n  0% {\r\n    opacity: 0;\r\n  }\r\n  100% {\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n@keyframes fadeOut {\r\n  0% {\r\n    opacity: 1;\r\n  }\r\n  100% {\r\n    opacity: 0;\r\n  }\r\n}\r\n\r\n/* loading window */\r\n#loading-window {\r\n  position: absolute;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  height: 100vh;\r\n  width: 100%;\r\n  top: 0;\r\n  z-index: 100;\r\n  backdrop-filter: blur(100px);\r\n  background-color: rgba(255, 255, 255, 0.349);\r\n}\r\n\r\n#loading-wrapper {\r\n  background-color: rgba(255, 255, 255, 0.5);\r\n  border-radius: 40px;\r\n  padding: 60px;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: 80px;\r\n}\r\n\r\n#loading-wrapper h2 {\r\n  font-size: 2rem;\r\n  color: #111111d0;\r\n}\r\n\r\n#loader {\r\n  width: 120px;\r\n  height: 120px;\r\n  border-radius: 50%;\r\n  display: inline-block;\r\n  border-top: 3px solid #ffffffe5;\r\n  border-right: 3px solid transparent;\r\n  box-sizing: border-box;\r\n  animation: rotation 1s linear infinite;\r\n}\r\n\r\n.fade-out-loading-animation {\r\n  animation: fadeOut 0.8s;\r\n}\r\n\r\n@keyframes rotation {\r\n  0% {\r\n    transform: rotate(0deg);\r\n  }\r\n\r\n  100% {\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n\r\n#app-icon {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  height: 40px;\r\n  width: 40px;\r\n}\r\n\r\n/* cloud images */\r\n.cloud-image {\r\n  position: absolute;\r\n  right: 2%;\r\n  bottom: 5%;\r\n  height: 40px;\r\n  width: 40px;\r\n}\r\n\r\n.main-wrapper {\r\n  position: relative;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n}\r\n\r\n.popup {\r\n  top: 0;\r\n  right: 20%;\r\n  width: 150px;\r\n  background-color: rgb(27, 52, 82);\r\n  padding: 10px;\r\n  color: rgb(228, 228, 228);\r\n  border-radius: 16px;\r\n  font-size: 1rem;\r\n  font-weight: 500;\r\n  box-shadow: var(--box-shadow);\r\n  animation: fadeIn 200ms ease-in-out;\r\n}\r\n\r\n#hours-per-week-popup-settings {\r\n  top: 0;\r\n}\r\n\r\n.header {\r\n  font-size: 2rem;\r\n  font-weight: 500;\r\n  color: var(--header-white);\r\n  text-align: center;\r\n  width: 340px;\r\n}\r\n\r\n.stopwatch-wrapper {\r\n  display: flex;\r\n  flex-direction: column;\r\n  margin-top: 50px;\r\n  gap: 20px;\r\n  align-items: center;\r\n}\r\n\r\n.stopwatch-time-elements-wrapper {\r\n  height: 100px;\r\n  width: var(--width-window-mainscreen);\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: 5px;\r\n  color: white;\r\n  background-color: rgba(84, 95, 125, 0.233);\r\n  border: 1px solid rgba(255, 255, 255, 0.055);\r\n  border-radius: 16px;\r\n  padding: 10px;\r\n  box-shadow: var(--box-shadow);\r\n}\r\n\r\n#hours-wrapper,\r\n#minutes-wrapper,\r\n#seconds-wrapper,\r\n#miliseconds-wrapper {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  font-size: 1.6rem;\r\n  width: 40px;\r\n}\r\n\r\n#hours-header,\r\n#minutes-header,\r\n#seconds-header,\r\n#milliseconds-header {\r\n  font-size: 1rem;\r\n}\r\n\r\n#miliseconds-wrapper {\r\n  justify-content: end;\r\n}\r\n\r\n.start-pause-reset-stopwatch-buttons-wrapper {\r\n  display: flex;\r\n  gap: 20px;\r\n}\r\n\r\n.stopwatch-number {\r\n  min-width: 30px;\r\n}\r\n\r\n.button {\r\n  font-family: var(--font-poppins);\r\n  font-size: 1rem;\r\n  font-weight: 500;\r\n  color: rgb(51, 51, 51);\r\n  width: 105px;\r\n  padding-block: 15px;\r\n  border-radius: 16px;\r\n  background-color: rgb(138, 221, 131);\r\n  border: 3px solid rgb(255, 255, 255);\r\n  transition-duration: 100ms;\r\n}\r\n\r\n.button:hover {\r\n  cursor: poin